.card{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(282px, 1fr));
  grid-template-rows: 1fr 1fr;
  gap: 20px 17px;
  list-style: none;
  width: 100%;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 1280px){
  .card{
    margin: auto;
  }
}

@media screen and (max-width: 618px){
   .card{
     margin: auto;
  }
}