.popup__form{
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-end;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 34px 36px;
  padding-bottom: 36px;
  margin: 0;
  background: white;
  color: black;
  outline: none;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (max-width: 700px){
  .popup__form{
    padding: 25px 22px;
    padding-bottom: 25px;
  }
}

@media screen and (max-height: 400px){
  .popup__form{
    padding: 20px 22px;
  }
}