.popup__title{
  flex: 1;
  overflow: hidden;
  padding: 0;
  margin: 0;
  font-size: 24px;
  font-weight: 900;
  line-height: 29px;
  text-overflow: ellipsis;
  white-space: nowrap;
  min-height: 70px;
}

@media (max-width: 535px){
  .popup__title{
    font-size: 20px;
    line-height: 25px;
  }
}